.animable {
  opacity: 0;
}
#freepik--Space--inject-1--inject-10 {
  animation: 4.3s 1 forwards cubic-bezier(0.36, -0.01, 0.5, 1.38) zoomIn,
    10s Infinite linear shake;
  animation-delay: 0s, 5.8s;
}

#freepik--Rocket--inject-1--inject-10 {
  animation: 4.4s 1 forwards cubic-bezier(0.36, -0.01, 0.5, 1.38) slideUp,
    3s Infinite linear floating;
  animation-delay: 0s, 4.4s;
}

#freepik--Meteorites--inject-1--inject-10 {
  animation: 6s Infinite linear floating;
  animation-delay: 0s;
}
@keyframes zoomIn {
  0% {
    opacity: 0;
    /* transform: scale(0.5); */
  }
  100% {
    opacity: 1;
    /* transform: scale(1); */
  }
}
@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }
  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}
@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: inherit;
  }
}
@keyframes floating {
  0% {
    opacity: 1;
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
